import { Cairo } from "next/font/google";
import "./globals.css";

const cairo = Cairo({
  subsets: ["arabic"],
  variable: "--font-cairo",
  weight: ["400", "700"], // يمكنك إضافة 300 أو 600 أو 800 حسب الحاجة
});

export const metadata = {
  title: "موقعي",
  description: "موقع رائع بخط Cairo",
};

export default function RootLayout({ children }) {
  return (
    <html lang="ar">
      <head>
        <link rel="icon" href="/icon.svg" />
      </head>
      <body className={cairo.variable}>
        {children}
      </body>
    </html>
  );
}
